import * as types from '../actions/actionTypes';

const initialState = {
  currentPage: 'home',
};

/**
 * State management for data related to menu
 * @param {Object} state - The current (or initial) state
 * @param {Object} action - An incoming action
 */
export default function menu(state: any = initialState, action: any) {
  switch (action.type) {
    case types.MENU_HOME: {
      return Object.assign({}, state, {
        currentPage: 'home',
      });
    }
    case types.MENU_SEARCH: {
      return Object.assign({}, state, {
        currentPage: 'search',
      });
    }
    case types.MENU_PROFILE: {
      return Object.assign({}, state, {
        currentPage: 'profile',
      });
    }
  }
}
